/* src/App.css */
#drum-machine {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #282c34;
}

#display {
  margin-bottom: 20px;
  font-size: 24px;
  text-align: center;
  width: 200px;
  padding: 10px;
  border: 2px solid black;
  color: white;
  background-color: #444;
}

.pads {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-gap: 10px;
}

.drum-pad {
  width: 100px;
  height: 100px;
  background-color: #444;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: 2px solid black;
  user-select: none;
}

.drum-pad:active {
  background-color: #666;
}
